.doctor_cover {
  margin: 0 auto;
  width: 960px;
  display: flex;
  justify-content: space-between;
}

.doctor_item {
  width: 370px;
  position: relative;
  margin: 0;
  padding: 0;
  height: auto;
  margin-bottom: 4px;
  overflow: hidden;
}

.doctor_name {
  padding-left: 6px;
  display: block;
  position: relative;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

.doctor_about_text {
  width: 530px;
}

.doctor_cover_item {
  padding-left: 40px;
}

.doctor_cover_item_spec::before {
  content: "";
  display: block;
  margin: 0;
  padding: 0;
  width: 25px;
  height: 25px;
  position: relative;
  background-position: center;
  background-size: cover;
  top: 24px;
  left: -40px;
  background-image: url(https://i.ibb.co/MGTk3T7/ico-position.png);
}
